import { ExcelService } from 'sections/reports/src/shared/services';
// Angular import
import { Component, OnInit, Input } from "@angular/core";

// Installed modules
import {
  NgbModal,
  ModalDismissReasons,
  NgbActiveModal,
  NgbModalRef
} from "@ng-bootstrap/ng-bootstrap";

// Global shared functions import
import { getUpdateFilter } from "shared/functions";
import { objectDateToTextDate, textDateToObjectDate } from "shared/functions";

// Global shared models
import { AlertModel } from "shared/models/helpers/Alert";
import { UserSelectionModel } from "shared/models";

// Global shared services
import { AlertService } from "shared/services";
import { UserSelectionService } from "shared/services/crud/system/user-selection.service";

ExcelService

// Local models
import { BackendResponseModel } from "sections/reports/src/shared/models/reports/backendResponse.model";

// Local shared
import { AgentsAssignationService } from "sections/reports/src/shared/services/reports/agents/reports-agents-assignation.service";
import { AgentsAssignationModel } from "sections/reports/src/shared/models/reports/agents/AgentsAssignation.model";
import { faUser } from "@fortawesome/free-solid-svg-icons";

import * as moment from "moment";


@Component({
  selector: "app-reports-assignation-report-list",
  templateUrl: "./assignation-report-list.component.html",
  styleUrls: ["./assignation-report-list.component.scss"]
})
export class AssignationReportListComponent implements OnInit {
  // Variables that come from main component
  @Input() userSelection: UserSelectionModel;
  @Input() selectorVisibleFields: UserSelectionModel;

  // Component variables
  alertMessage = new AlertModel();

  // Selector variables
  local_store;

  // Realtime variables
  timerConnected;

  // Datatable variables
  show_columns = false; // shows or hides a colum
  show = false; // shows or hides the table
  selected = [];

  // Filter variables
  numberOfRowsInTable;
  filterFieldList;
  initialSelectedFilterField;
  findInList;

  // Variable to display values
  model: AgentsAssignationModel;
  rows;
  rows_original;
  rows_total;
  rows_detail;
  rows_detail_original;
  rows_detail_total;
  row_selection;
  exportNameGroup;
  exportNameDetail;
  rows_valid;

  //Icons
  faUser = faUser;

  // Modal window variables
  activeModal: NgbActiveModal;


  sortFn;
  agent_legal_id;
  
  // Init
  constructor(
    private agentsAssignationService: AgentsAssignationService,
    private alertService: AlertService,
    private modalService: NgbModal,
    private userSelectionService: UserSelectionService,
    private excelService: ExcelService
  ) {
    this.model = new AgentsAssignationModel();
    this.local_store = "assignation";
    this.selectorVisibleFields = new UserSelectionModel("visible");
    this.selectorVisibleFields.assignation = false;
    this.selectorVisibleFields.auxiliar = false;
  }

  // Start
  ngOnInit() {
    this.userSelection = this.userSelectionService.readUserSelectionHistoric(
      this.local_store
    );
    this.getReportList(this.userSelection);
    this.filterFieldList = this.model.fieldList();
    this.numberOfRowsInTable = { id: 10, value: 10 };
    this.exportNameGroup = "reporte-asignación-consolidado";
    this.exportNameDetail = "reporte-asignación-detalle";

    this.initialSelectedFilterField = {
      field_name: "agent_name",
      name: "nombre_agente",
      text: "Nombre agente"
    };
  }

  // Finish
  ngOnDestroy() {
    this.userSelectionService.writeUserSelectionHistoric(
      this.userSelection,
    );
  }

  // Get records from backend
  getReportList(userSelection) {
    if (userSelection) {
      this.rows = [new AgentsAssignationModel()];
      this.agentsAssignationService.getReportList(userSelection).subscribe(
        (res: BackendResponseModel) => {
          this.show = false;

          this.timerConnected = 0;
          // console.error("res", res);

          if (Array.isArray(res.detail)) {
            this.rows = res.group;
            this.rows_original = res.group;
            this.rows_total = res.total;

            this.rows_detail = res.detail;
            this.rows_detail_original = res.detail;

            this.show = true;
          } else {
            console.error("Error", res);
          }
          this.alertMessage = new AlertModel();
        },
        error => {
          console.error("Error", error);
          this.show = false;
          this.alertService.error(error.status);
          this.alertMessage.alertTitle = "Error del servidor";
          this.alertMessage.alertText = error.statusText;
          this.alertMessage.alertShow = true;
          this.alertMessage.alertClass =
            "alert alert-danger alert-dismissible fade show";
        }
      );
    }
  }

  // Show modal detail window
  openDetailModal(content, selected) {
    // console.error("selected-onActivate", this.row_selection);

    this.rows_detail = this.rows_detail_original.filter(x => {
      return x.agent_id === this.row_selection.agent_id;
    });

    this.rows_detail_total = this.rows_original.filter(x => {
      return x.agent_id === this.row_selection.agent_id;
    });

    this.activeModal = this.modalService.open(content, {
      windowClass: "my-class",
      keyboard: false
    });
  }

  // Data table activate
  onActivate(event) {
    this.row_selection = event.row;
    if (event.type === "dblclick") {
    }
  }
  // Datatable select
  onSelect(event) {
    this.selected = event.selected;
  }

  // Update on return of selector in header
  onReturnHeaderResult(event) {
    this.ngOnInit();
  }

  // Response report finder to display number of rows in table
  onReturnNumberOfRowsInTable(event) {
    console.error("event", event);
    this.numberOfRowsInTable = event;
  }

  // Response report finder
  onReturnRowsForTable(event) {
    this.rows = event;
  }

  // Export group to excel
  exportToExcelGroup(data) {
    const filterData = data.map(x => {
      return {
        conectado: x.connected,
        previo: x.previous_day,
        nombre_agente: x.agent_name,
        cant: x.times_registered,
        id_agente: x.agent_id,
        duracion: x.duration_time,
        fecha_inicial: x.min_date,
        fecha_final: x.max_date,
        hora_inicial: x.start_time,
        hora_final: x.end_time,
        segundos_conexion: x.duration_time_secs
      };
    });

    this.excelService.exportAsExcelFile(filterData, this.exportNameGroup);
  }

  // Export detail to excel
  exportToExcelDetail(data) {
    const filterData = data.map(x => {
      return {
        id: x.record_id,
        id_agente: x.agent_id,
        nombre_agente: x.agent_name,
        auxiliar: x.break_name,
        duracion: x.duration_time,
        segundos_conexion: x.duration_time_secs,
        fecha_inicial: x.min_date,
        fecha_final: x.max_date,
        hora_inicial: x.start_time,
        hora_final: x.end_time,
      };
    });

    this.excelService.exportAsExcelFile(filterData, this.exportNameDetail);
  }

  // Helper function to expose detail fields from a row
  onObjectToArray(data) {
    let obj = data[0];

    let output;
    if (obj !== undefined) {
      output = Object.entries(obj).map(([key, value]) => ({
        key,
        value
      }));
    }
    return output;
  }

  // temporary method to generate excel map for exporting model
  onCreateModel(model?) {
    model = new AgentsAssignationModel().fieldList();

    console.error("model", model);

    let obj = {};

    model.map(x => {
      obj[`${x.name}`] = "x." + x.field_name;
    });

    let newModel = JSON.stringify(obj);
    let newModel2 = JSON.stringify(
      newModel
        .replace(/\"/g, "")
        .replace(/:/g, ": ")
        .replace(/,/g, ",\n ")
    );
    let model3 = eval(newModel2);

    console.error("model", model3);
  }

  //Test function for modal
  openModal(content) {
    this.rows_detail = this.rows_detail_original.filter(x => {
      return x.agent_id === this.row_selection.agent_id;
    });

    this.rows_detail_total = this.rows_original.filter(x => {
      return x.agent_id === this.row_selection.agent_id;
    });

    this.activeModal = this.modalService.open(content, {
      windowClass: "my-class",
      keyboard: false
    });
  }
}
